import RowImages from "app/collections/row-images";
import template from "templates/row-manager";
import rowTemplate from "templates/row";

const RowImageView = Backbone.View.extend({
    tagName: "img",
    className: "row-image",

    initialize: function(options) {
        this.options = options;
        Vent.on("restored", this.handleRestored, this);
    },

    render: function() {
        this.$el.attr("src", this.model.get("src"));
        this.$el.draggable({
            helper: "clone",
            revert: "invalid"
        });
        this.$el.on("load", e => {
            this.model.set({
                visible: true,
                imageEl: this.el
            });
            this.options.parentView.updateSizes();
        });

        return this;
    },

    handleRestored: function($el, collection) {
        if ($el.is(this.$el)) {
            collection.add(this.model);
            this.model.set({notInQueue: false});
            this.collection.remove(this.model);
        }
    },

    updateSize: function() {
        let parentPos = this.options.parentView.$el.position(),
            selfPos = this.$el.position();

        this.model.set({
            position: {
                left: parentPos.left + selfPos.left + 2,
                top: parentPos.top + selfPos.top + 2
            },
            size: {
                width: this.el.width,
                height: this.el.height
            }
        });
    }
});

const RowView = Backbone.View.extend({
    className: "row",
    parentView: null,

    sizes: {
        left: 20,
        top: 20,
        width: 500,
        height: 100
    },

    events: {
        "click .remove": "removeRow"
    },

    initialize: function(opts) {
        this.collection = new RowImages();
        this.collection.on("add", this.addItem, this);
        this.collection.on("remove", this.removeItem, this);
        this._views = {};
        this.parentView = opts.parentView;
    },

    render: function() {
        let _this = this;
        this.$el.html(rowTemplate());
        this.$el.css({
            position: "absolute",
            left: "20px",
            top: "20px"
        });
        this.$el.droppable({
            hoverClass: "highlighted",
            accept: ".image-queue-item",
            drop: function(event, ui) {
                Vent.trigger("dropped", ui.draggable, _this.collection);
                console.log(_this.collection)
            }
        });
        this.$el.resizable({
            handles: "all",
            stop: () => {
                this.updateSizes();
                this.updateMySize();
            }
        });
        this.$el.draggable({
            containment: "parent",
            stop: () => {
                this.updateSizes();
                this.updateMySize();
            }
        });

        return this;
    },

    addItem: function(model) {
        model.set({rowEl: this.$el});
        let view = this._views[model.cid] = new RowImageView({
            model,
            collection: this.collection,
            parentView: this
        });
        this.$(".images").append(view.render().el);
    },

    removeItem: function(model) {
        this._views[model.cid].remove();
        delete this._views[model.cid];
    },

    removeRow: function() {
        Vent.trigger("batchRestore", this.collection);
        this.parentView.removeRow(this);
        this.collection.reset();
        this.remove();
    },

    updateMySize: function() {
        this.sizes = Object.assign({}, this.sizes);
        this.sizes.height = this.$el.css('height');
        this.sizes.width = this.$el.css('width');
        this.sizes.left = this.$el.css('left');
        this.sizes.top = this.$el.css('top');
        this.parentView.updateSizes(this, this.sizes);
        this.updateSizes();
    },

    loadMySize: function(sizes) {
        this.$el.css('height', sizes.height);
        this.$el.css('width', sizes.width);
        this.$el.css('left', sizes.left);
        this.$el.css('top', sizes.top);
        this.sizes = Object.assign({}, this.sizes);
        this.sizes.height = sizes.height;
        this.sizes.width = sizes.width;
        this.sizes.top = sizes.top;
        this.sizes.left = sizes.left;
    },

    updateSizes: function() {
        _.each(this._views, view => {
            view.updateSize();
        });
    }
});

export default Backbone.View.extend({
    className: "row-manager",

    events: {
        "click .btn-add-row": "addRow",
        "click .btn-preview": "preview"
    },

    initialize: function() {
        this._views = [];
    },

    render: function() {
        this.$el.html(template());
        return this;
    },

    addRow: function() {
        let view = new RowView({
            parentView: this
        });
        this._views.push(view);
        this.$el.append(view.render().el);
    },

    removeRow: function(row) {
        this._views.splice(this._views.indexOf(row), 1);
    },

    preview: function(e) {
        let $el = this.$(e.currentTarget),
            active = $el.hasClass("active");

        $el.toggleClass("active", !active);

        this._views.forEach(view => {
            view.$el.toggleClass("preview", !active);
        });
    },

    updateSizes: function (view, sizes) {
        if(this._views.indexOf(view) != -1) {
            this._views[this._views.indexOf(view)].sizes = sizes;
        }
    },

    loadRows: function(rows) {
        rows.forEach((row) => {
            let view = new RowView({
                parentView: this
            });
            this._views.push(view);
            this.$el.append(view.render().el);
            row.arr.forEach((el) => {
                view.collection.add(el);
            });
            view.loadMySize(row.sizes);
        });
    }

});



