export default Backbone.View.extend({
    tagName: "canvas",

    initialize: function(options) {
        this.options = options;
    },

    render: function() {
        let {size, images} = this.options.manifest;
        let catalog = {
            background: [],
            foreground: [],
            people: []
        };
        this.ctx = this.el.getContext('2d');

        // Size height/width
        this.el.width = size.width;
        this.el.height = size.height;

        _.each(images, image => {
            catalog[image.get("type")].push(image);
        });

        console.log("catalog", catalog);

        // Drag Images
        [
            ...catalog.background,
            ...catalog.people.sort((a, b) => {
                let aH = a.get("size").height,
                    bH = b.get("size").height;

                if (aH < bH) {
                    return -1;
                } else if (aH > bH) {
                    return 1;
                }

                return 0;
            }),
            ...catalog.foreground
        ].forEach(this.drawImage.bind(this))

        return this;
    },

    drawImage: function(image) {
        let calculated = image.get("calculated");
        let imageEl = image.get("imageEl");

        this.ctx.drawImage(
            imageEl,
            calculated.position.left,
            calculated.position.top,
            calculated.size.width,
            calculated.size.height
        );
    },

    getDataURL: function() {
        return this.el.toDataURL("image/jpeg");
    },

    getBlob: function(callback) {
        return this.el.toBlob(callback, "image/jpeg");
    }
});



