let textFile;
let template = m => [
    `bfkey:`,
    `key: peruskey`,
    `digi: convert BACKGROUND \( INPUT -resize x${m.width} -virtual-pixel Transparent +distort Affine "0,0 ${m.left},${m.top} 0,${m.height} ${m.left},${m.top + m.height}" FOREGROUND -hald-clut -unsharp 0.5x0.5+1.0+0.02 \) -flatten FOREGROUND -flatten -profile /root/Dropbox/TF_templates/key/sRGB.icm OUT.jpg`,
    `print:`,
    `crop: convert INPUT -crop ${m.masterWidth}x${m.masterHeight}+0+0 -resize OUT.jpg`,
    `web: 1|1`,
    `out: 1`
].join("\n");

export default class ManifestFile {
    text = "";

    constructor(manifest) {
        this.data = this.makeTextContent(manifest);
    }

    makeTextContent(manifest) {
        // Get size and images from manifest
        let {size, images} = manifest;

        // Get background, foreground and person information
        let background = images.filter(image => image.get("type") === "background")[0],
            foreground = images.filter(image => image.get("type") === "foreground")[0],
            person = images.filter(image => image.get("type") === "people")[0],
            calculated = person.get("calculated");

        // Make the text template
        let text = template({
            masterWidth: size.width,
            masterHeight: size.height,
            backgroundName: background.get("name"),
            foregroundName: foreground ? foreground.get("name") : "",
            personName: foreground ? foreground.get("name") : "",
            left: calculated.position.left,
            top: calculated.position.top,
            width: calculated.size.width,
            height: calculated.size.height
        });
        this.text = text;

        return new Blob([text], {type: "text/plain"});
    }

    getDataURL() {
        if (textFile) {
            window.URL.revokeObjectURL(textFile);
        }
        textFile = window.URL.createObjectURL(this.data);
        return textFile;
    }
}



