import "app/lib/jquery-ui";
import template from "templates/image";

const HEIGHT_MARGIN = 0;

export default Backbone.View.extend({
    className: "image-draggable",

    events: {
        "click .remove": "removeImage"
    },

    initialize: function() {
        this.model.on("change:size change:position", this.handleResize, this);
    },

    render: function() {
        this.$el.html(template(this.model.toJSON()));
        this.setup();

        return this;
    },

    setup: function() {
        let {clientWidth, clientHeight} = $(".images-container")[0];
        let $img = this.$("img");
        var self = this;
        var fromFile = (this.model.attributes.size.width);

        $img.on("load", (e) => {
            let {width, height} = e.target;
            let owidth = width, oheight = height;

            // if img is loaded from file
            if(fromFile) {
                owidth = this.model.attributes.originalSize.width;
                width = this.model.attributes.size.width;
                oheight = this.model.attributes.originalSize.height;
                height = this.model.attributes.size.height;
            }
            let aspect = width / height;

            // Save original w/h
            this.model.set({
                originalSize: {width: owidth, height: oheight},
                imageEl: e.target,
                visible: true
            });

            if (height + HEIGHT_MARGIN > clientHeight && !fromFile) {
                height = clientHeight - HEIGHT_MARGIN;
                width = height * aspect;
            }

            let left = (clientWidth - width) / 2;
            let top = (clientHeight - height) / 2;
            // if img is loaded from file
            if(fromFile) {
                left = this.model.attributes.position.left;
                top = this.model.attributes.position.top;
            }
            // Set size and position
            this.$el.width(width);
            this.$el.height(height);
            this.$el.css({
                left: `${left}px`,
                top: `${top}px`
            });

            // Set model
            this.model.set({
                position: {left, top},
                size: {width, height}
            });

            // Add classes and events
            this.$el.addClass("active");
            if (this.model.get("type") === "people") {
                //add filters
                console.log(this.model);
                if(this.model.attributes.filters  && !_.isEmpty(this.model.attributes.filters)) {
                    Vent.trigger('changeViewFilters', this.model.attributes.filters);
                    Vent.trigger('changeFilters', this.model.attributes.filters);
                }

                this.$el.draggable({
                    cancel: ".remove",
                    containment: "parent",
                    drag: (e, ui) => {
                        this.model.set({
                            position: ui.position
                        })
                    }
                });
                this.$el.resizable({
                    aspectRatio: aspect,
                    maxWidth: clientWidth,
                    maxHeight: clientHeight,
                    handles: "se, nw",
                    resize: (e, ui) => {
                        this.model.set({
                            size: ui.size,
                            position: ui.position
                        })
                    }
                });
            }
        });
    },

    removeImage: function() {
        this.model.destroy();
        this.remove();
    },

    handleResize: function(model, newSize) {
        const { width, height } = newSize;
        const { left, top } = newSize;
        if (typeof width !== "undefined" && typeof height !== "undefined") {
            this.$el.width(width);
            this.$el.height(height);
        } else {
            this.$el.css({
                left: `${left}px`,
                top: `${top}px`
            });
        }
    }
});



