import "app/lib/blob";
import template from "templates/layout";
import ImageView from "app/views/image";
import ImageQueueView from "app/views/image-queue";
import CanvasView from "app/views/canvas";
import ImageFile from "app/models/image";
import Images from "app/collections/images";
import ManifestFile from "app/views/manifest";
import RowManagerView from "app/views/row-manager";
import { saveAs } from "app/lib/file-saver";

import { body_full } from "app/models/body-full"
import { body_medium } from "app/models/body-medium"
import { body_closeup } from "app/models/body-closeup"
var _ = require('lodash');

let currentUrl = window.location.href; 
if(__DEV__) {
    currentUrl = "http://images.tf/template/100001";
}
var defaultFilters = {
    hue: 0,
    saturation: 100,
    brightness: 100
};

export default Backbone.View.extend({
    el: "#app",

    filters: Object.assign({}, defaultFilters),

    events: {
        "dragover": "handleDragOver",
        "dragleave": "handleDragOver",
        "drop": "handleDrop",
        "click .btn-editing .btn": "handleEditingClick",
        "click .btn-exporting .export-image": "handleExportImage",
        "click .btn-exporting .export-src": "handleExportSrc",
        "click .btn[data-mode]": "handleModeChange",
        "click .add-full": "handleAddFull",
        "click .add-medium": "handleAddMedium",
        "click .add-closeup": "handleAddCloseup",
        "click .btn-exporting .load-src": "handleLoadSrc",
        "click .back-load-src": "handleBackLoadClick",
        "click .fore-load-src": "handleForeLoadClick",
        "change .back-load-src": "handleBackgroundLoad",
        "change .fore-load-src": "handleForegroundLoad",
        "click .js-hide-help": "hideHelp",
        "click .js-show-help": "showHelp",
        "change .js-hue": "onChangeHue",
        "change .js-saturation": "onChangeSaturation",
        "change .js-brightness": "onChangeBrightness",
        "click .btn-reset-filters": "onResetFilters"
    },

    onResetFilters: function () {
        Vent.trigger('changeViewFilters', defaultFilters);
        Vent.trigger('changeFilters', defaultFilters);
    },

    onChangeHue: function (e) {
        var value = e.currentTarget.value;
        Vent.trigger('changeFilters', {hue: value});
    },

    onChangeSaturation: function (e) {
        var value = e.currentTarget.value;
        Vent.trigger('changeFilters', {saturation: value});
    },

    onChangeBrightness: function (e) {
        var value = e.currentTarget.value;
        Vent.trigger('changeFilters', {brightness: value});
    },

    onChangeFilters: function (values) {
        Object.assign(this.filters, values);
        var f = this.filters;
        this.$(".people").css({filter: `hue-rotate(${f.hue}deg) saturate(${f.saturation}%) brightness(${f.brightness}%)`});
        if(_.isEqual(f, defaultFilters)) {
            $('.btn-reset-filters').addClass('disabled');
        } else {
            $('.btn-reset-filters').removeClass('disabled');
        }
    },

    onChangeViewFilters: function (values) {
        if(typeof values.hue !== 'undefined') $('.js-hue').val(values.hue);
        if(typeof values.saturation !== 'undefined') $('.js-saturation').val(values.saturation);
        if(typeof values.brightness !== 'undefined') $('.js-brightness').val(values.brightness);
    },

    initialize: function() {
        this.mode = Mode.FREE;
        
        // Image Scene
        this.collection = new Images();
        this.collection.on("add", this.renderImage, this);
        this.collection.on("add remove", this.onAddRemove, this);
        this.collection.on("reset", this.render, this);


        Vent.on("changeFilters", this.onChangeFilters, this);
        Vent.on("changeViewFilters", this.onChangeViewFilters, this);

        this.render();
        this.getMode();
    },

    render: function() {
        this.$el.html(template({
            activeMode: this.mode
        }));

        if (this.mode === Mode.FORMATION) {
            this.imageQueue = new ImageQueueView({
                collection: new Images(),
                parentEl: this
            });
            this.rowManager = new RowManagerView();
            this.$(".containers").append(this.imageQueue.render().el);
            this.$(".people").append(this.rowManager.render().el);
        } else {
            if (this.imageQueue) {
                this.imageQueue.remove();
                this.imageQueue = false;
            }
            if (this.rowManager) {
                this.rowManager.remove();
                this.rowManager = false;
            }
        }

        // Window resize handler:
        window.addEventListener("resize", this.handleResize.bind(this));
    },

    hideHelp: function() {
        $('.tutorial').slideUp(400, function() {
            $('.js-show-help').removeClass('hide');
        });
    },

    showHelp: function() {
        $('.tutorial').slideDown(400);
        $('.js-show-help').addClass('hide');
    },

    renderImage: function(model) {
        if ((this.mode === Mode.FREE) || this.mode === Mode.FORMATION && model.get("type") !== "people") {
            let view = new ImageView({model: model});
            this.$(`.${model.get("type")}`).append(view.render().el);
        } else if (this.mode === Mode.FORMATION && model.get("type") === "people") {
            if(!model.attributes.notInQueue) this.imageQueue.collection.add(model);
            else {
                $('<img src="'+ model.attributes.src +'">').on('load', (e) => {
                    model.set({imageEl: e.target});
                });
            }
        }
    },

    handleAddFull: function(e) {
        this.collection.add(body_full);
    },
    handleAddMedium: function(e) {
        this.collection.add(body_medium);
    },
    handleAddCloseup: function(e) {
        this.collection.add(body_closeup);
    },

    handleBackLoadClick: function(e) {
        if($('.load-background-label .btn').hasClass('disabled')) e.preventDefault();
    },
    handleForeLoadClick: function(e) {
        if($('.load-foreground-label .btn').hasClass('disabled')) e.preventDefault();
    },

    onAddRemove: function() {
        this.setDragGuides();
        this.toggleExportManifest();
    },

    setDragGuides: function() {
        (this.collection.where({type: "background"}).length > 0)
            ? $('.drag-guide [data-type="background"]').css('visibility', 'hidden')
            : $('.drag-guide [data-type="background"]').css('visibility', 'visible');

        (this.collection.where({type: "foreground"}).length > 0)
            ? $('.drag-guide [data-type="foreground"]').css('visibility', 'hidden')
            : $('.drag-guide [data-type="foreground"]').css('visibility', 'visible');

        (this.collection.where({type: "people"}).length > 0 && window.mode=='group')
            ? $('.drag-guide [data-type="people"]').css('visibility', 'hidden')
            : $('.drag-guide [data-type="people"]').css('visibility', 'visible');
    },

    handleDragOver: function(e) {
        e = e.originalEvent;
        e.preventDefault();
        e.stopPropagation();
        if (e.dataTransfer) {
            e.dataTransfer.dropEffect = "copy";

            let $drag = this.$(".drag-guide");

            $drag.addClass("active");
            $drag.find(".active").removeClass("active");
            $drag.find(e.target).addClass("active");
        }
    },

    handleDrop: function(e) {
        e = e.originalEvent;
        e.preventDefault();
        e.stopPropagation();

        if (e.dataTransfer) {
            let files = e.dataTransfer.files;
            let $target = $(e.target);

            _.each(files, file => {
                if (file.type.match(/image.*/)) {
                    let reader = new FileReader();
                    reader.onload = this.handleFileReadLoad.bind(
                        this,
                        file,
                        $target.data("type"),
                        reader
                    );
                    reader.readAsDataURL(file);
                }
            });

            // Remove drag guide
            this.$(".drag-guide").removeClass("active");
        }
    },

    handleFileReadLoad: function(fileInfo, type, reader) {
        let image = new ImageFile(fileInfo);
        image.set({
            src: reader.result,
            type: type
        });
        this.collection.add(image);
    },

    handleEditingClick: function(e) {
        let $el = $(e.currentTarget);
        let $parent = $el.parents(".btn-group");

        $parent.find(".btn").removeClass("active");
        $el.addClass("active");
        this.$(".images-container > .active").removeClass("active");
        this.$(`.images-container > .${$el.data("type")}`).addClass("active");
    },

    handleExportImage: function(e) {
        e.preventDefault();

        let canvas = (new CanvasView({
            manifest: this.collection.getImagesManifest()
        })).render();

        canvas.getBlob(blob => {
            saveAs(blob, "export.jpg");
        });
    },

    handleExportManifest: function(e) {
        // on but click
        let manifest = new ManifestFile(this.collection.getImagesManifest());

        let $link = $(e.currentTarget);
        $link.attr("href", manifest.getDataURL());
        $link.attr("download", "manifest.txt");
    },

    handleModeChange: function(e) {
        let mode = this.$(e.currentTarget).data("mode");
        this.mode = Mode.enumValueOf(mode);
        this.collection.reset();
    },

    toggleExportManifest: function() {
        let backgroundCount = this.collection.where({type: "background"}).length,
            foregroundCount = this.collection.where({type: "foreground"}).length,
            peopleCount = this.collection.where({type: "people"}).length;

        let isSingle = (backgroundCount === 1 && foregroundCount === 1 && peopleCount === 1);
        let isExportable = (backgroundCount > 0 && peopleCount > 0 );
        let isSrcExportable = (backgroundCount >= 1 && foregroundCount >= 0 && peopleCount > 0 );

        this.$(".export-manifest").toggleClass("disabled", !isSingle);
        this.$(".export-image").toggleClass("disabled", !isExportable);
        this.$(".export-src").toggleClass("disabled", !isSrcExportable);

        this.$(".add-full").toggleClass("disabled", !!peopleCount || window.mode=='group');
        this.$(".add-medium").toggleClass("disabled", !!peopleCount || window.mode=='group');
        this.$(".add-closeup").toggleClass("disabled", !!peopleCount || window.mode=='group');

        this.$(".load-background-label .btn").toggleClass("disabled", !!backgroundCount);
        this.$(".load-foreground-label .btn").toggleClass("disabled", !!foregroundCount);
    },

    handleBackgroundLoad: function (e) {
        e.preventDefault();
        e.stopPropagation();

        if (e.target.files && e.target.files.length) {
            var files = [e.target.files[0]];
            let $target = $(e.target);

            _.each(files, file => {
                if (file.type.match(/image.*/)) {
                    let reader = new FileReader();
                    reader.onload = this.handleFileReadLoad.bind(
                        this,
                        file,
                        "background",
                        reader
                    );
                    reader.readAsDataURL(file);
                }
            });
        }
    },

    handleForegroundLoad: function (e) {
        e.preventDefault();
        e.stopPropagation();

        if (e.target.files && e.target.files.length) {
            var files = [e.target.files[0]];
            let $target = $(e.target);

            _.each(files, file => {
                if (file.type.match(/image.*/)) {
                    let reader = new FileReader();
                    reader.onload = this.handleFileReadLoad.bind(
                        this,
                        file,
                        "foreground",
                        reader
                    );
                    reader.readAsDataURL(file);
                }
            });
        }
    },

    getMode: function() {
        $('.preloader').removeClass('hide')
        $.get(currentUrl + "/mode", (data) => {
            $('.preloader').addClass('hide')
            if(data=='group') {
                window.mode = 'group';
                this.mode = Mode.enumValueOf("FORMATION");
                this.collection.reset();
                this.handleLoadGroupSrc();
            }
            else if(data=='individual') {
                window.group = 'individual';
                this.mode = Mode.enumValueOf("FREE");
                this.handleLoadSingleSrc();
            }
            else {
                throw new Error('mode isnt specified')
            }
        })
    },

    handleExportSrc: function (e) {
        $('.preloader').removeClass('hide');
        if(this.rowManager && this.rowManager._views) var rows = _.cloneDeep(this.rowManager._views);
        this.collection.setRealCoors();
        //debugger;
        if(rows && rows.length) {
            rows.forEach((row) => {
                this.collection.setRealCoors(row.collection);
            })
        }
        var data = _.cloneDeep(this.collection.models);
        data.forEach((el) => {
            el.attributes.imageEl = null;
            el.attributes.rowEl = null;
            // add filters for persons
            if(el.attributes.type === 'people') {
                el.attributes.filters = this.filters;
            }
        });
        if(rows && rows.length) {
            rows = rows.map((row) => {
                return {sizes: row.sizes, arr: row.collection.models, filters: this.filters};
            });

            rows.forEach((row) => {
                row.arr.forEach((el) => {
                    el.attributes.imageEl = null;
                    el.attributes.rowEl = null;
                });
            });
            data.forEach(d => {
                d.attributes.notInQueue = false;
            });

            rows.forEach(row => {
                row.arr.forEach(pers => {
                    data.forEach(d => {
                        if(d.attributes.name == pers.attributes.name) {
                            d.attributes.notInQueue = true;
                            d.attributes.real = pers.attributes.real;
                        }
                    })
                })
            })
        }

        //let manifest = new ManifestFile(this.collection.getImagesManifest());

        //let manifestData = manifest.text;
        let manifestData = "";


        try {
            var back = (this.collection.where({type: "background"}))[0].attributes.src;
        } catch (e) { back = "" }
        try {
            var fore = (this.collection.where({type: "foreground"}))[0].attributes.src;
        } catch(e) {fore = ""}
        var text = JSON.stringify({
            "data": data, "mode": this.mode,
            "rows": rows
            //background: back,
            //foreground: fore,
            //manifest: manifestData
        });
        
        $.post(currentUrl + "/data", text, function(data) {
            $('.preloader').addClass('hide')
            alert("Template has been saved!")
        })
    },

    handleLoadSingleSrc: function (e) {
        $('.preloader').removeClass('hide')
        $.get(currentUrl + "/data", (data) => {
            $('.preloader').addClass('hide');

            if(data) {
                try {
                    data = JSON.parse(data);
                } catch(e) {
                    alert('Error parsing data');
                    data = {};
                }
                var mode = data.mode.name;
                var rows = data.rows;
                $('button[data-mode='+mode+']').trigger('click');
                this.addLoadedFiles(data.data);
                this.toggleExportManifest();
            }
        })
    },

    handleLoadGroupSrc: function (e) {
        $('.preloader').removeClass('hide')
        $.get(currentUrl + "/data", (data) => {
            $.get(currentUrl + "/images", (images) => {
                $('.preloader').addClass('hide');

                if(data) {
                    try {
                        data = JSON.parse(data);
                    } catch(e) {
                        alert('Error parsing data');
                        data = {};
                    }

                    var mode = data.mode.name;
                    var rows = data.rows;
                }
                data = data || {};
                data.data = data.data || [];

                //filters
                data.data.forEach(el => {
                    if(el.filters && !_.isEmpty(el.filters)) {
                        Vent.trigger('changeViewFilters', el.filters);
                        Vent.trigger('changeFilters', el.filters);
                    }
                });

                if(images) {
                    images = JSON.parse(images);
                    if(images.data) images.data.forEach((el) => {
                        var added = false;
                        if(data && data.data) data.data.forEach((d) => {
                            if(el.filename == d.name) added = true;
                        });
                        
                        if (!added) data.data.push({
                            imageEl: null,
                            lastModified: 1465313460000,
                            lastModifiedDate: "2016-06-07T15:31:00.000Z",
                            name: el.filename,
                            notInQueue: false,
                            originalSize: {
                                height: 100,
                                width: 100
                            },
                            position: {
                                left: 0,
                                top: 0
                            },

                            size: {
                                height: 100,
                                width: 100
                            },
                            src: el.url,
                            type: "people",
                            visible: true,
                            webkitRelativePath: ""
                        });
                    });
                }

                this.addLoadedFiles(data.data);
                if(rows && rows.length) {
                    this.rowManager.loadRows(rows);
                }
                this.toggleExportManifest();

            })
        })
    },

    addLoadedFiles(data) {
        data.forEach((el) => {
            this.collection.add(el);
        });
        window.setTimeout(() => {
            this.handleResize();
        }, 500);
    },

    removePeopleModel: function(model) {
        //this.collection.remove(model);
        model.set("notInQueue", true);
    },

    addPeopleModel: function (model) {
        this.collection.models
            .filter((el) => el.attributes.src == model.attributes.src)[0]
            .set("notInQueue", false);
    },

    handleResize: function() {
        this.collection.centerAlign();
    }

});



