import RowImages from "app/collections/row-images";
import template from "templates/image-queue";
import imageTemplate from "templates/image";

const ImageQueueItem = Backbone.View.extend({
    tagName: "li",
    className: "image-queue-item",
    parentEl: null,

    initialize: function(opts) {
        this.parentEl = opts.parentEl;
        Vent.on("dropped", this.handleDropped, this);
    },

    render: function() {
        this.$el.html(imageTemplate(this.model.toJSON()));
        this.$el.draggable({
            helper: "clone",
            revert: "invalid"
        });
        this.$("img").load(e => {
            this.model.set({
                visible: false,
                originalSize: {
                    width: e.target.naturalWidth,
                    height: e.target.naturalHeight
                }
            });
        });
        return this;
    },

    handleDropped: function($el, collection) {
        if ($el.is(this.$el)) {
            collection.add(this.model);
            this.collection.remove(this.model);
            this.parentEl.removePeopleModel(this.model);
            this.remove();
        }
    }
});

export default Backbone.View.extend({
    className: "image-queue",
    parentEl: null,

    initialize: function(opts) {
        this.parentEl = opts.parentEl;
        this.collection = new RowImages();
        this.collection.on("add", this.addItem, this);
        Vent.on("batchRestore", this.addItems, this);
    },

    render: function() {
        let _this = this;
        this.$el.html(template());
        this.$el.droppable({
            hoverClass: "highlighted",
            accept: ".row-image",
            drop: function(event, ui) {
                Vent.trigger("restored", ui.draggable, _this.collection);
            }
        });
        return this;
    },

    addItems: function(collection) {
        collection.each(model => {
            this.collection.add(model);
            this.parentEl.addPeopleModel(model);
        });
    },

    addItem: function(model) {
        let view = new ImageQueueItem({
            model: model,
            collection: this.collection,
            parentEl: this.parentEl
        });
        this.$("ul").prepend(view.render().el);
    }

});



