import Image from "app/models/image";

const unitless = str => parseFloat(str.replace(/[^\d.]/g, ""));

export default Backbone.Collection.extend({
    model: Image,

    getBenchmarkBg: function() {
        let backgrounds = this.filter(m => m.get("type") === "background");
        return this.filter(m => m.get("type") === "background")[0];
            //.max(m => m.get("originalSize").width * m.get("originalSize").height);
    },

    getImagesManifest: function() {
        let benchmark = this.getBenchmarkBg();
        let scaleFactor = benchmark.get("originalSize").height / benchmark.get("size").height;
        let {top: benchTop, left: benchLeft} = benchmark.get("position");

        // Get visible images
        let filteredImages = this
            .filter(model => model.get("visible") === true)
            .map(model => {
                let {top, left} = model.get("position");
                let {width, height} = model.get("size");

                return model.set({
                    calculated: {
                        position: {
                            top: parseInt((top - benchTop) * scaleFactor, 10),
                            left: parseInt((left - benchLeft) * scaleFactor, 10)
                        },
                        size: {
                            width: parseInt(width * scaleFactor, 10),
                            height: parseInt(height * scaleFactor, 10)
                        }
                    }
                });
            });

        return {
            images: filteredImages,
            size: benchmark.get("originalSize")
        };
    },
    
    setRealCoors: function(coll) {
        var collection = coll || this;
        let benchmark = this.getBenchmarkBg();
        let scaleFactor = benchmark.get("originalSize").height / benchmark.get("size").height;
        let {top: benchTop, left: benchLeft} = benchmark.get("position");
        //debugger;

        collection
            .forEach(model => {
                let {top, left} = model.get("position");
                let {width, height} = model.get("size");
                if(!model.get('notInQueue') && window.mode=="group") {
                    model.set({
                        real: {
                            position: {
                                top: 0,
                                left: 0
                            },
                            size: {
                                width: 0,
                                height: 0
                            }
                        }
                    });
                    return
                }
                //debugger;
                var rowLeft = 0;
                var rowTop = 0;
                console.log(model.get('rowEl'));
                if(window.mode == "group" && model.get('rowEl')) {
                    model.set({
                        real: {
                            position: {
                                top: parseInt((top - benchTop) * scaleFactor, 10),
                                left: parseInt((left - benchLeft) * scaleFactor, 10)
                            },
                            size: {
                                width: parseInt(width * scaleFactor, 10),
                                height: parseInt(height * scaleFactor, 10)
                            }
                        }
                    });
                    //rowLeft = parseInt(model.get('rowEl').css('left')) + parseInt(model.get('rowEl').css('width'))/2;
                    //rowTop = parseInt(model.get('rowEl').css('top'));
                    return;
                }

                model.set({
                    real: {
                        position: {
                            top: parseInt((top - benchTop + rowTop) * scaleFactor, 10),
                            left: parseInt((left - benchLeft + rowLeft) * scaleFactor, 10)
                        },
                        size: {
                            width: parseInt(width * scaleFactor, 10),
                            height: parseInt(height * scaleFactor, 10)
                        }
                    }
                });
            });
    },

    centerAlign: function() {
        const benchmark = this.getBenchmarkBg();
        if (!benchmark) {
            return;
        }
        const { width: benchWidth, height: benchHeight } = benchmark.get("size");
        const { left: benchLeft } = benchmark.get("position");
        const { clientWidth, clientHeight } = $(".images-container")[0];

        const scaleFactor = clientHeight / benchHeight;
        const imageAspect = benchWidth / benchHeight;
        const proposedWidth = clientHeight * imageAspect;
        const newLeft = (clientWidth - proposedWidth) / 2;
        const margin = newLeft - benchLeft; 

        _.each(this.models, model => {
            let { width, height } = model.get("size");
            let { left, top } = model.get("position");

            model.set({
                size: {
                    width: width * scaleFactor,
                    height: height * scaleFactor
                },
                position: {
                    left: margin + (left * scaleFactor),
                    top: top * scaleFactor
                }
            });
        });

        const rowManager = global.layoutView.rowManager;

        if (rowManager && rowManager._views) {
            const rowViews = rowManager._views;
            
            _.each(rowViews, view => {
                const sizes = view.sizes;
                view.loadMySize({
                    width: `${unitless(sizes.width) * scaleFactor}px`,
                    height: `${unitless(sizes.height) * scaleFactor}px`,
                    top: `${unitless(sizes.top) * scaleFactor}px`,
                    left: `${margin + (unitless(sizes.left) * scaleFactor)}px`
                });
                view.updateSizes();
            });
        }
    }
})



